.footer {
  padding: 40px 0;
  text-align: center;
  /* background-color: var(--light-2); */
  color: var(--cinder);
}

.socialIcon:not(:last-child) {
  margin-right: 32px;
}

.socialIcon {
  font-size: 25px;
  cursor: pointer;
}

.socialIcon:hover {
  opacity: 0.7;
}

@media only screen and (max-width: 1024px) {
  .footer {
    padding:30px 0px;
  }
}
